.App{
  display: block;
  margin: auto;
  padding: auto;
  padding: 10px;
}
.rooms{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.card-container{
  padding: 5px;
}
.card { 
  border:1px solid #e7e7e7;
  border-radius: 10px;
  width: 100%;
}
.card-disabled{
  background-color: #00000044;
  z-index: 100;
}
.card-head {
  background-color: #e7e7e7;
  border-radius: 8px 8px 0px 0px;
  padding: 2px 16px;
}
.card-body {
  display: -webkit-flex;
  display: flex;
}
.item {
  padding: 10px;
}
.btns{
  padding: 5px;
}
